<template>
    <a-menu
        mode="inline"
        theme="dark"
        :selectedKeys="selectedKeys"
        :openKeys="openKeys"
        @click="menuClick"
    >
        <a-menu-item key="/center/role">
            <template #icon>
                <UserOutlined style="font-size: 18px"></UserOutlined>
            </template>
            {{ $t("role") }}
        </a-menu-item>
        <a-menu-item key="/center/fight">
            <template #icon>
                <FireOutlined style="font-size: 18px"></FireOutlined>
            </template>
            {{ $t("fight") }}
        </a-menu-item>
        <a-menu-item key="/center/backpack">
            <template #icon>
                <InboxOutlined style="font-size: 18px"></InboxOutlined>
            </template>
            {{ $t("backpack") }}
        </a-menu-item>
        <a-sub-menu key="/center/dongtian" :title="$t('dongtian')" disabled>
            <template #icon>
                <Iconfont type="wx-dongtian" />
            </template>
            <a-menu-item key="/center/dongtian/house">
                <span> - </span>
                <span style="margin-inline-start: 10px">
                    {{ $t("house") }}
                </span>
            </a-menu-item>
        </a-sub-menu>
        <a-sub-menu key="/center/sect" :title="$t('sect')" disabled>
            <template #icon>
                <Iconfont type="wx-sect" />
            </template>
            <a-menu-item key="/center/mySect">
                <span> - </span>
                <span style="margin-inline-start: 10px">
                    {{ $t("mySect") }}
                </span>
            </a-menu-item>
        </a-sub-menu>
        <a-menu-item key="/center/rank">
            <template #icon>
                <TrophyOutlined style="font-size: 18px"></TrophyOutlined>
            </template>
            {{ $t("rank") }}
        </a-menu-item>
        <a-menu-item key="/center/setting">
            <template #icon>
                <ToolOutlined style="font-size: 18px"></ToolOutlined>
            </template>
            {{ $t("setting") }}
        </a-menu-item>
        <a-menu-item key="/center/logout">
            <template #icon>
                <LogoutOutlined style="font-size: 18px"></LogoutOutlined>
            </template>
            {{ $t("logout") }}
        </a-menu-item>
    </a-menu>
</template>
<script>
import Iconfont from "@/components/Iconfont.vue";
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import {
    UserOutlined,
    FireOutlined,
    InboxOutlined,
    TrophyOutlined,
    ToolOutlined,
    LogoutOutlined,
} from "@ant-design/icons-vue";

export default {
    name: "CenterMenu",
    components: {
        Iconfont,
        UserOutlined,
        FireOutlined,
        InboxOutlined,
        TrophyOutlined,
        ToolOutlined,
        LogoutOutlined,
    },
    setup() {
        const router = useRouter();
        const state = reactive({
            collapsed: false, // 菜单开合
            selectedKeys: [""], // 当前选中项
            openKeys: [""], // 当前展开项
        });

        onMounted(() => {
            // 计算menu参数
            computedMenuAttr();
        });

        // 计算menu参数
        const computedMenuAttr = () => {
            // 当前选中项
            state.selectedKeys = [router.currentRoute.value.path];
            // 当前展开项
            let currentRoute = router.currentRoute.value.path;
            let currentRouteList = currentRoute.split("/");
            currentRouteList.pop();
            let currentRouteText = currentRouteList.join("/");
            state.openKeys = [currentRouteText];
        };

        // 菜单点击
        const menuClick = async (item, key, keyPath) => {
            if (item.key === "logout") {
                sessionStorage.clear();
                await router.push("/");
            } else {
                await router.push(item.key);
                computedMenuAttr();
            }
        };
        return {
            ...toRefs(state),
            menuClick,
        };
    },
};
</script>
<style lang="scss" scoped></style>
