<template>
    <div class="center">
        <Header></Header>
        <div class="content">
            <div class="menu">
                <CenterMenu></CenterMenu>
                <!--                <a-menu-->
                <!--                    :selectedKeys="selectedKeys"-->
                <!--                    :openKeys="openKeys"-->
                <!--                    mode="inline"-->
                <!--                    theme="dark"-->
                <!--                    :inline-collapsed="false"-->
                <!--                    :items="menuList"-->
                <!--                    @click="menuClick"-->
                <!--                >-->
                <!--                </a-menu>-->
            </div>
            <div class="sider">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>

<script>
import { useRouter } from "vue-router";
import { reactive, toRefs, onMounted, h, watch } from "vue";
import centerMenu from "@/assets/config/centerMenu.js";
import { useI18n } from "vue-i18n";
import { MenuFoldOutlined, MenuUnfoldOutlined } from "@ant-design/icons-vue";
import CenterMenu from "@/components/CenterMenu.vue";

export default {
    name: "Center", // 主页框架
    components: {
        CenterMenu,
    },
    setup() {
        const router = useRouter();
        const { t, locale } = useI18n();
        const state = reactive({
            collapsed: false, // 菜单开合
            selectedKeys: [""], // 当前选中项
            openKeys: [""], // 当前展开项
            menuList: centerMenu,
        });

        onMounted(async () => {
            // 根据语言修改 menu label
            changeLocaleLabel();
            // 计算menu参数
            computedMenuAttr();
        });

        // 监听语言改变
        watch(
            locale,
            () => {
                changeLocaleLabel();
            },
            { deep: true }
        );

        // 根据语言修改 menu label
        const changeLocaleLabel = () => {
            for (let key in state.menuList) {
                let text = t(state.menuList[key].name);
                state.menuList[key].label = text;
                state.menuList[key].title = text;
                if (state.menuList[key].children) {
                    for (let keys in state.menuList[key].children) {
                        text = t(state.menuList[key].children[keys].name);
                        state.menuList[key].children[keys].label = text;
                        state.menuList[key].children[keys].title = text;
                    }
                }
            }
        };

        // 计算menu参数
        const computedMenuAttr = () => {
            // 当前选中项
            state.selectedKeys = [router.currentRoute.value.path];
            // 当前展开项
            let currentRoute = router.currentRoute.value.path;
            let currentRouteList = currentRoute.split("/");
            currentRouteList.pop();
            let currentRouteText = currentRouteList.join("/");
            state.openKeys = [currentRouteText];
        };

        // 菜单点击
        const menuClick = async (item, key, keyPath) => {
            if (item.key === "logout") {
                sessionStorage.clear();
                await router.push("/");
            } else {
                await router.push(item.key);
                computedMenuAttr();
            }
        };

        // 菜单开合
        const toggleCollapsed = () => {
            state.collapsed = !state.collapsed;
            console.log(state.collapsed);
            // state.openKeys = state.collapsed ? [] : state.preOpenKeys;
        };

        return {
            ...toRefs(state),
            menuClick,
            MenuUnfoldOutlined,
            MenuFoldOutlined,
            toggleCollapsed,
        };
    },
};
</script>

<style lang="scss" scoped>
.center {
    height: 100vh;

    .content {
        display: flex;

        .menu {
            width: 256px;
            background-color: #001529;
        }

        .sider {
            width: calc(100vw - 256px);
            height: calc(100vh - 100px);
        }
    }
}
</style>
